import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import "./Navbar.css"
import CssBaseline from "@mui/material/CssBaseline";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
// import svg from "../assets/svg/x-twitter.svg"



const drawerWidth = 245;
const navItems = [

 
  [
    <Button sx={{ color: " #ddd", backgroundColor: "transparent", textTransform: "uppercase", fontSize: 13 }}>
     
     <a href="#about">
     About
     </a>
    </Button>
  ],
  [
    <Button sx={{ color: " #ddd", backgroundColor: "transparent", textTransform: "uppercase",   fontSize: 13 }}>
    <a href="#services">Services</a>
    </Button>
  ], 
  [
    <Button sx={{ color: " #ddd", backgroundColor: "transparent", textTransform: "uppercase",  fontSize: 13 }}>
     <a href="#certificates" > Certificate</a>
    </Button>
  ],
  [
    <Button sx={{ color: " #ddd", backgroundColor: "transparent", textTransform: "uppercase",  fontSize: 13 }}>
   <a href="#projects">Projects</a>
    </Button>
  ],

];

export default function DrawerAppBar(props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const drawer = (
    <Box
      onClick={handleDrawerToggle}
      sx={{
        textAlign: "center",
        p: 0,
        color: "#ccc",
        backgroundColor: "#0A152F",
        height: 1000,
      }}
    >
      <Typography
        sx={{
          mr: 5.3,
          p: 2,

        }}
      >
      
       ABDULAZEEZ <span>SAIDU</span>
      
      </Typography>
      <Divider sx={{ backgroundColor: "#fff" }} />
      <List>
        {navItems.map((item) => (
          <ListItem
            key={item}
            sx={{ p: 0, m: 0 }}
          //disablePadding
          >
            <ListItemButton >
              <ListItemText primary={item} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar sx={{ color: "#ccc", backgroundColor: "#0A152F" }}>
        <Toolbar>
          <IconButton
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 0.8, display: { sm: "none" } }}>
            <MenuIcon sx={{ color: "#fff", border: "1.4px solid crimson", borderRadius: "2.5px" }} />
          </IconButton>
          <Typography sx={{ color: "crimson" }} > PORT
          </Typography>
          {/*  */}
          <Typography sx={{ mr: "auto", }} > <span >FOLIO</span>
          </Typography>

          {/* <Box></Box> */}
          <Box sx={{ display: { xs: "none", sm: "block" } }}>
            {navItems.map((item) => (
              <Button key={item} >{item}</Button>
            ))}
          </Box>
        </Toolbar>
      </AppBar>
      <nav>
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
        >
          {drawer}
        </Drawer>
      </nav>
      {/* <Box component="main" sx={{ p: 3 }}>
      </Box> */}
    </Box>
  );
}
